.main-view-container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    overflow: auto;
}

.content-box {
    display: flex;
    /* height: 500px; */
    width: 800px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

#profile-image {
    margin: 1em;
    width: 250px;
}

.clickable:hover {
    cursor: pointer;
    color:rgb(0, 0, 255);
}

.clickable {
    color: rgb(0, 0, 184);
}

#profile-image {
    height: 60%;
    border: 10px solid black;
    border-radius: 2px;
}

.text-container {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin-left: 50px;
    font-size: 120%;
}

.social-icon {
    margin-left: 5px;
    height: 20px;
}